.actual-events {
  &__event {
    width: 100%;
    margin-bottom: 60px;
  }

  &__img {
    width: 100%;
    height: 100%;
    max-height: 360px;
    margin-bottom: 20px;

    @include biggerScreen {
      max-height: 520px;
    }

    @extend %hover-effect-img;

    object-fit: cover;
    object-position: center;
  }
}
